<template>
  <div class='locations container-fluid'>
    <div class='container'>
      <div class='row mb-4'>
        <div class='col-sm'>
          <select id='countries' name='countries'
              @change="countrySelected" v-model='selectedCountry'>
            <option v-for='country in countries' :key='country.name' v-bind:value='country.code'>
              {{ country.name }}
            </option>
          </select>
        </div>
      </div>
      <div class='row'>
        <div class='col-sm'>
          <input class='form-control' type='text' placeholder='Enter a location...'
              @keyup='fetchSuggestedLocations' v-model='location' :disabled='!supportedCountry'>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import suggestion from '../service/suggestion'

export default {
  name: 'locations',
  data () {
    return {
      location: '',
      locations: [],
      selectedCountry: 'TH',
      supportedCountry: true,
      countries: [
        { name: 'Brunei', code: 'BN' },
        { name: 'Cambodia', code: 'KH' },
        { name: 'Indonesia', code: 'ID' },
        { name: 'Laos', code: 'LA' },
        { name: 'Malaysia', code: 'MY' },
        { name: 'Myanmar', code: 'MM' },
        { name: 'Philippines', code: 'PH' },
        { name: 'Singapore', code: 'SG' },
        { name: 'Thailand', code: 'TH' },
        { name: 'Vietnam', code: 'VN' }
      ],
      url: `${this.$config.serviceHost}${this.$config.suggestionService}`
    }
  },
  methods: {
    countrySelected() {
      suggestion.clearCache()
      this.supportedCountry = suggestion.isCountrySupported(this.selectedCountry)
    },

    fetchSuggestedLocations(event) {
      const location = this.location.trim()
      if (location.length < 3) {
        return
      }

      let suggestions = suggestion.getFromCache(location.toLowerCase())
      if (!suggestions) {
        suggestions = suggestion.searchFor(`${this.url}/${this.selectedCountry}/${location}`)
      }

      //showSuggestions(suggestions)
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped>
  .locations {
    font-size: 1.3em;
    padding-left: 10px;
    padding-top: 30px;
  }
  input {
    background-color: #343A40;
    border: 0;
    color: #687f7f;
    font-size: 1.3em;
    padding: 20px;
  }
  select {
    font-size: 1.3em;
    padding: 10px;
    width: 100%;
  }
</style>



